.wrapper {
  overflow: hidden;
}
.btn-download__windows {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(90deg, #0D9ECC 0.39%, #0051AF 100%);
  border-radius: 30px;
  color: #F8FAFF;
  i {
    flex-shrink: 0;
    width: 18px;
    margin-right: 10px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      fill: currentColor;
    }
  }
  span {
    font-size: 16px;
    line-height: 120%;
  }
  &:hover {
    background: linear-gradient(90deg, #0051AF 0.39%, #0D9ECC 100%);
  }
}
.btn-download__mac {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 1px solid #434B5C;
  border-radius: 6px;
  color: #CED2DC;
  i {
    flex-shrink: 0;
    width: 18px;
    margin-right: 10px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      fill: currentColor;
    }
  }
  span {
    font-size: 16px;
    line-height: 120%;
  }
  &:hover {
    background: #434B5C;
  }
}
.btn-green {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient(90deg, #11D147 0.39%, #189625 100%);
  box-shadow: 0px 10px 30px 0px rgba(18, 197, 64, 0.40);
  border-radius: 30px;
  color: #FBFBFB;
  i {
    flex-shrink: 0;
    max-width: 20px;
    margin-right: 14px;
    svg {
      max-width: 100%;
      height: auto;
    }
  }
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
  }
  &:hover {
    background: linear-gradient(90deg, #189625 0.39%, #11D147 100%);
  }
}
.display2 {
  font-size: 44px;
  line-height: 130%;
  color: #FBFBFB;
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.60);
}
.swiper-pagination {
    &-bullet {
      width: 16px;
      height: 16px;
      background: transparent;
      border: 2px solid #434B5C;
      opacity: 1;
    }
  &-bullet-active {
    width: 52px;
    background: #1C77FF;
    border-color: transparent;
    border-radius: 8px;
  }
}
.swiper-button-prev {
  transform: rotate(-180deg);
  width: 40px;
  height: auto;
  margin-top: 0;
  img {
    width: 100%;
  }
  &:after {
    display: none;
  }
}
.swiper-button-next {
  width: 40px;
  height: auto;
  margin-top: 0;
  img {
    width: 100%;
  }
  &:after {
    display: none;
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    font-size: 32px;
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    font-size: 28px;
  }
}